
.Node{outline:solid;outline-color:grey;outline-width:2px;background-color:#fff;z-index:1}.Node:hover{cursor:pointer}
.Tree{background-color:transparent;position:relative;display:flex;justify-content:space-evenly;flex-direction:column;height:400px}@-webkit-keyframes activate{0%{outline-color:grey;stroke:grey}to{outline-color:gold;stroke:gold}}@keyframes activate{0%{outline-color:grey;stroke:grey}to{outline-color:gold;stroke:gold}}.Tree .active{-webkit-animation:activate 1.5s forwards;animation:activate 1.5s forwards}.Tree-Row{display:flex;justify-content:space-evenly}
.Link-Container{position:absolute;top:0;left:0;width:100%;height:100%}.Link{fill:none;stroke:grey;stroke-width:6px}